import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { BookingsService } from 'src/app/rest-service.service';

@Component({
  selector: 'app-display-car',
  templateUrl: './display-car.component.html',
  styleUrls: ['./display-car.component.scss']
})
export class DisplayCarComponent implements OnInit {
  car;
  bookings;
  constructor(@Inject(MAT_DIALOG_DATA) private data, private bookingsService:BookingsService ) {
  
   }

  ngOnInit(): void {
    this.car = this.data.car;
    console.log(this.car);

    this.bookingsService.getAllBookings({car:this.car._id}).subscribe((bookings)=>{
      this.bookings = bookings;
    })
  }

}
