import { Component, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { EventEmitter } from '@angular/core';
import { from } from 'rxjs';
import { BookingsService } from 'src/app/rest-service.service';
import {Booking} from '../../../models/booking';

@Component({
  selector: 'ngbd-modal-content',
  templateUrl:"newbooking.modal.html" 
})
export class NgbdModalContent implements OnInit{
  
  @Output()
  bookingChange = new EventEmitter<string>();

  isUpdate:boolean = false;
  modalTitle = "Make new Booking";

  @Input() booking: Booking;
  bookingForm = new FormGroup(
    {
      date_from : new FormControl(),
      date_to : new FormControl(),
      car_id : new FormControl(),
      pickup_point : new FormControl(),
      dropoff_point : new FormControl(),
      client_name : new FormControl(''),
      telephone: new FormControl(),
      address : new FormControl(),
      paid : new FormControl()
    }
  );

  constructor(public activeModal: NgbActiveModal, private fb: FormBuilder,private bookingsService : BookingsService) {}
  ngOnInit(): void {
    if(this.booking){
      // console.log("exoume stoixeia");
      // console.log(this.booking);
      let booking = {
        client_name: this.booking.client_name ?? "",
        address: this.booking.address ?? "",
        car_id:this.booking.car.name??'',
        date_from: this.booking.date_from ?? "",
        date_to: this.booking.date_to ?? "",
        paid: this.booking.paid??"",
        _id: this.booking._id ?? "",
        dropoff_point: this.booking.dropoff_point ?? "",
        pickup_point: this.booking.pickup_point ?? "",
        telephone: this.booking.telephone ?? ""
      }

      this.bookingForm = this.fb.group(booking);
      this.isUpdate = true;
    }else{
      console.log('den exoume stoixeia');
    }


    if(this.isUpdate){
      this.modalTitle = "Show/Update booking";
    }
  }

  
  showLog(){
    console.log(this.bookingForm.value);
  }

  deleteBooking(bookingId:string){

    let confirm = window.confirm('The record is about to be deleted forever. You want to proceed?');

    if(!confirm) return;

    this.bookingsService.deleteBooking(bookingId).subscribe(response=>{
      this.bookingChange.emit('delete');
      this.activeModal.close();
    })
  }

  createBooking(){

    //get the data from form
    const booking:Booking = this.bookingForm.value;

    console.log(booking);

    this.bookingsService.createBooking(booking).subscribe(response=>{
      console.log(response);
      this.bookingChange.emit('create');
      this.activeModal.close();

    });

  }

  updateBooking(){
    console.log('updating');
    const booking = this.bookingForm.value;

    const bookingId = booking._id;
    this.bookingsService.updateBooking(bookingId,booking)
    .subscribe(response=>{
      console.log(response);
      this.bookingChange.emit('update');
      this.activeModal.close();
    });
  }
}
