import { validateHorizontalPosition } from '@angular/cdk/overlay';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { BookingsService } from 'src/app/rest-service.service';
import { SelectCarComponent } from '../select-car/select-car.component';

@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.scss']
})
export class CreateComponent implements OnInit {

  bookingForm;

  constructor(private dialog: MatDialog, private fb: FormBuilder, private bookingsService : BookingsService,private router: Router) { }
  selectedCar;

  ngOnInit(): void {
    this.bookingForm = this.fb.group({
      client_name:['', Validators.required],
      address:['', Validators.required],
      pickup_point:['', Validators.required],
      dropoff_point:['', Validators.required],
      date_from:['', Validators.required],
      date_to:['', Validators.required],
      telephone:['', Validators.required],
      paid:false
    });

  }

  selectCar(){
    let params={}

    if(this.bookingForm.controls.date_from.value.length>1 && this.bookingForm.controls.date_to.value.length>1){
      console.log('exoume setare');
      params ={
        date_to: new Date(this.bookingForm.controls.date_to.value).toISOString(),
        date_from: new Date( this.bookingForm.controls.date_from.value).toISOString()
      } 

    }

    let modalRef = this.dialog.open(SelectCarComponent,{data:params});


    modalRef.afterClosed().subscribe(car=>{
      console.log(car);
      if(car){
        this.selectedCar = car[0];
      }
    });
  }

  submitForm(){
    if(!this.selectedCar) return;
    let booking = this.bookingForm.value;
    booking.car = this.selectedCar._id;
    console.log(booking);

    this.bookingsService.createBooking(booking).subscribe(data=>{
      console.log('Added');
      console.log(data);
      this.router.navigate(['bookings','show']);
    })
  }

}
