import { Component, Inject, Injectable, OnInit } from '@angular/core';
import { dateInputsHaveChanged } from '@angular/material/datepicker/datepicker-input-base';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Car } from 'src/app/models/car';
import { BookingsService } from 'src/app/rest-service.service';

@Component({
  selector: 'app-select-car',
  templateUrl: './select-car.component.html',
  styleUrls: ['./select-car.component.scss']
})
export class SelectCarComponent implements OnInit {
  cars:Car[];

  private params={};
  private checkAvailable:Boolean = false;

  selectedCar =null;
  constructor(private carsService: BookingsService, @Inject(MAT_DIALOG_DATA) public data) { 

    if(data.date_from && data.date_to){

      this.params = data;
      this.checkAvailable = true;
    }
  }

  ngOnInit(): void {

    if(this.checkAvailable){
      this.carsService.getAvailableCars(this.params).subscribe(cars=>{
        this.cars = cars;
      })
    }
    else{
      this.carsService.getAllCars().subscribe(cars=>{
        this.cars = cars;
      })
    }

  }

}
