import { Injectable } from '@angular/core';
import {  HttpClient} from "@angular/common/http";
import { from, Observable, of } from 'rxjs';
import { Booking } from "./models/booking";
import BOOKINGS from './models/bookings_list';
import { Car } from "./models/car";
import CARS from './models/cars_list';

const bookings:Booking[] = BOOKINGS;
const cars : Car[] = CARS;

const fromServer = true;

@Injectable({
  providedIn: 'root'
})
export class BookingsService {
  // endpoint = 'http://localhost:3000/bookings/';
  // car_endpoint = 'http://localhost:3000/cars/';
  endpoint = 'https://anemosrent.herokuapp.com/bookings/';
  car_endpoint = 'https://anemosrent.herokuapp.com/cars/';
  constructor(private http: HttpClient) { }

  getAllBookings(params):Observable<any>{
    if(fromServer){
      return this.http.get(this.endpoint,{
        params:params
      });
    }
    return of (BOOKINGS);
  }

  getPastBookings(params):Observable<any>{
    if(fromServer){
      return this.http.get(this.endpoint+'past',{
        params:params
      });
    }
    return of (BOOKINGS);
  }

  getFutureBookings(params):Observable<any>{
    if(fromServer){
      return this.http.get(this.endpoint+'future',{
        params:params
      });
    }
    return of (BOOKINGS);
  }

  getActiveBookings(params):Observable<any>{
    if(fromServer){
      return this.http.get(this.endpoint+'active',{
        params:params
      });
    }
    return of (BOOKINGS);
  }

  

  getAllCars(): Observable<any>{
    if(fromServer){
      return this.http.get(this.car_endpoint);
    }
    return of(CARS);
  }

  createBooking(booking: Booking):Observable<any>{
    if(fromServer){
      return this.http.post(this.endpoint, booking);
    }
    return of (BOOKINGS);
  }

  updateBooking(bookingId:string, values){
    if(fromServer){
      return this.http.put(this.endpoint+bookingId, values);
    }
    return of (BOOKINGS);
  }

  deleteBooking(bookingId:string): Observable<any>{
    if(fromServer){
      return this.http.delete(this.endpoint+bookingId);
    }
    return of (BOOKINGS);
  }

  getAvailableCars(params):Observable<any>{
    if(fromServer){
      return this.http.get(this.car_endpoint+"available", {
        params: params
      });
    }
    return of (CARS);
  }

  createCar(car):Observable<any>{
    if(fromServer){
      return this.http.post(this.car_endpoint, car);
    }
    return of (CARS[0]);
  }
}
