import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Car } from '../../../models/car';
import { BookingsService} from '../../../rest-service.service';
import { CarDialogComponent } from '../car-dialog/car-dialog.component';
import { DisplayCarComponent } from '../display-car/display-car.component';

@Component({
  selector: 'app-cars-list',
  templateUrl: './cars-list.component.html',
  styleUrls: ['./cars-list.component.scss']
})
export class CarsListComponent implements OnInit {
  cars: Car[];

  constructor(private carsService: BookingsService, private dialog: MatDialog) { }

  ngOnInit(): void {
    this.reloadCars();
  }
  
  
  reloadCars(){
    this.carsService.getAllCars().subscribe(data=>{
      console.log(data);
      this.cars = data;
    });
  }

  createCarDialog(){
    let dialogRef = this.dialog.open(CarDialogComponent);
    
    dialogRef.afterClosed().subscribe(car=>{
      if(!car) return;

      this.carsService.createCar(car).subscribe(data=>{
        this.reloadCars();
      })
    });
  }

  displayCar(car){
    let dialogRef = this.dialog.open(DisplayCarComponent,{data: {car:car}});
    
  }
}
